<template>
    <div style="padding-top:46px">
        <van-nav-bar @click-left="$router.back()" left-arrow :title="'所有房间（ ' + num + ' ）'"   :right-text="(active == 0||active == 1)?(showCheckbox ? '退房' : '批量退房'):''" 
         @click-right="clickTui" ></van-nav-bar>
        
       <div id="scrollBox" style="overflow-y:auto;height:calc(100% - 46px)">
            <van-tabs @change="changeTab" color="#02A7F0" class="tabs" v-model="active" type="card">
                <van-tab name="0" title="全部" :disabled="showCheckbox"></van-tab>
                <van-tab name="1" title="已入住"></van-tab>
                <van-tab name="2" title="闲置" :disabled="showCheckbox"></van-tab>
            </van-tabs>

            <p class="zhushi">
                <span style="color:#F56C6C"><van-icon name="wap-home" /> 已入住</span>
                <span style="color:#67C23A"><van-icon name="wap-home" /> 闲置</span>
            </p>


            <div class="content">
                 <van-list
                          v-model="loading"
                          :finished="finished"
                          finished-text=""
                          @load="onLoad"
                        >
                <div class="contentBox">

                    
                    <div class="item chuizis" @click="clickItem(item,index,item.F_HotelRoomID)" :style="{'backgroundColor':item.F_CheckInPersonalNum > 0 ? '#F56C6C' : '#67C23A'}" 
                             v-for="(item,index) in baseData" :key="index">
                        <van-checkbox v-if="showCheckbox" icon-size="18px" checked-color="#02A7F0" v-model="item.active" shape="square"></van-checkbox>
                        

                        <p>{{item.F_RoomNumber}}</p>
                        <!-- v-if="item.userNum > 0" -->
                        <p style="font-size: 0.8rem;">({{item.F_CheckInPersonalNum}}人)</p>
                    </div>

               
                </div>

                 </van-list>
            </div>


            <div style="width:calc(100% - 3.2rem);margin:1rem auto;text-align:center">
                <!-- <van-button v-if="active == 0||active == 1" :style="{'width':showCheckbox ? '8rem' : '100%'}" style="margin-right:0.5rem" color="#02A7F0" @click="clickTui">{{showCheckbox ? '全部退房' : '批量退房'}}</van-button> -->
                <van-button v-if="showCheckbox" style="width:8rem;" color="#02A7F0" @click="clickCancel">取消</van-button>
            </div>
       </div>
    </div>
</template>

<script>
import { Toast,Dialog } from 'vant';
import {hotellist,checkOut} from "@/api/house";

    export default {
        data() {
            return {
                showCheckbox:false,
                active:0,
                baseData:[],
                loading:false, 
                finished:false,   
                page:0,
                num:""
            }
        },

        created(){

        },
    methods: {
       //退房
        checkOut(){
            let parans = {
                check_in_record_id: this.$route.query.F_HotelRoomID,
                };
                checkOut(parans).then((res) =>{
                    console.log(res)
                }).catch((err)=>{
                    console.log(err)
                })
        },
      onLoad(){
       this.page++;
         let parans = {
           page: this.page,
           check_in_personal_num:this.active==0?"":this.active==1?1:0
        };
         hotellist(parans).then((res) => {

            if(res.code==0){
               this.baseData = [...this.baseData, ...res.list]; //列表

               this.num = res.total;
               this.loading = false;

               // 数据全部加载完成
              if (res.list.length < 10) {
                this.finished = true;
              }

            }else{

                this.$api.msg('错误错误错误错误错误错误错误错误')
            }
          
        })
    },
            clickItem(item,index,F_HotelRoomID){
                if(this.showCheckbox){
                    console.log(11);
                    this.baseData[index].active = !this.baseData[index].active;
                }else{
                    this.$router.push({
                        path:"/register/roomDetail",
                        query:{
                            roomKey:item.F_RoomNumber,
                            userNum:item.F_CheckInPersonalNum,
                            F_HotelRoomID:F_HotelRoomID
                        }
                    })
                }
            },
            changeTab(name){
                this.finished = false;
                this.page = 0;
                this.baseData = [];

                // let arr = this.getRooms();
                // switch (name) {
                //     case "0":
                //         // this.baseData = arr
                //         break;
                //     case "1":
                //         this.baseData = arr.filter(item =>item.userNum > 0)
                //         break;
                //     case "2":
                //         this.baseData = arr.filter(item =>item.userNum == 0)
                //         break;
                // }
            },
            clickTui(){
                console.log(this.showCheckbox);
                if(!this.showCheckbox){
                    // this.showCheckbox = true
                    // document.querySelector("#scrollBox").scrollTop = 0
                    // this.$nextTick(() =>{
                    //     this.active = 1
                    // })

                        this.$router.push({
                        path:"/register/batchretreatroom"
                        })

                    console.log(this.active);
                }else{
                    let arr = this.baseData.filter(item =>item.active)
                    if(arr.length > 0){
                        Dialog.confirm({
                            title: '批量退房',
                            message: '确定将选中的房间退房吗？',
                            })
                        .then(() => {
                            // on confirm
                            Toast.loading({
                                message: '加载中...',
                                forbidClick: true,
                                duration:1000,
                                onClose:() =>{
                                    Toast.success('退房成功');
                                    // this.clickCancel()
                                    this.checkOut()
                                }
                            });
                        })
                        .catch(() => {
                            // on cancel
                        });
                    }else{
                        Toast('请选择房间退房');
                    }
                }
            },

            clickCancel(){
                this.showCheckbox = false
                this.baseData.forEach(item =>{
                    item.active = false
                })
                this.$nextTick(() =>{
                    this.active = 0
                })
            },
            getRooms() {
   
     
                let arr = Array.from({length:49}).map((item,index) =>{
                    return {
                        roomKey: (parseInt(index / 10) + 1) * 1000 + (index % 10 > 0 ? index % 10 : 0) + 1,
                        userNum: Math.ceil(Math.random() * 4) - 1,
                        active:false
                    }
                })
                return arr
            }
        },
        mounted () {
            // this.baseData = this.getRooms();
        },
    }
</script>

<style lang="scss" scoped>
.van-nav-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
.content{
    margin-top: 1rem;
    padding: 0 1rem;
    .contentBox{
        width: 100%;
        display: flex;
        justify-content:center;
        flex-wrap:wrap;
        .item{
            float: left;
            width: 3.5rem;
            height: 3.5rem;
            padding: 0.3rem 0.3rem 0.3rem 0.3rem;
            margin: 0.28rem;
            p{
                margin: 0;
                color: #fff;
                text-align: center;
                font-size: 1rem;
                line-height: 1.4rem;
            }
        }
    }
}

.tabs{
    margin: 1rem 0;
}

.zhushi{
    margin: 0;
    text-align: center;
    span{
        &:nth-child(1){
            margin-right: 4rem;
        }
    }
    .van-icon{
        position: relative;
        top: 0.15rem;
    }
}
</style>